import React from 'react';
import { Routes, Route } from 'react-router-dom';

import CoursesList from './CoursesList';
import CourseAddEdit from './CourseAddEdit';

const Courses = () => {

    const isWaiting = () => {
        return (
            <div className="lazy-loader">
                Loading...
                {/* <img src={asset_loader} alt="Loading" /> */}
            </div>
        );
    };

    return (
        <div className="container-fluid p-4">
            <React.Suspense fallback={isWaiting()}>
                <Routes>
                    <Route path='/' element={<CoursesList />} />
                    <Route path='/new-course' element={<CourseAddEdit />} />
                    <Route path='/view-course/:courseId' element={<CourseAddEdit />} />
                    <Route path='/edit-course/:courseId' element={<CourseAddEdit />} />
                </Routes>
            </React.Suspense>
        </div>
    );
};

export default Courses;