    // const modifyString = (label) => {
    //     return label.replace(/[^a-zA-Z ]/g, '').replace(/\s/g, '-').toLowerCase();
    // }

    const handleChange = (e, item = null, subIndx = null, tIndex = null) => {
        const { name, value, checked } = e.target;
        if (item === 'custom-field') {
            let tempCf = [...customFields];
            tempCf[subIndx][name] = value;
            setCustomFields(tempCf);
        } else if (item === 'enable-hero') {
            setPost({ ...post, [name]: checked })
        } else {
            setPost({ ...post, [name]: value });
        }
    }

    const handleEditorResponse = useCallback((res) => {
        const { name, value } = res;
        setPost((prevPost) => ({ ...prevPost, [name]: value }));
    }, []);

    const fileUpload = async (event) => {
        if (event.target.files.length > 0) {
            let tempPost = { ...post };
            let attachment = {
                fileName: event.target.files[0].name,
            }
            const fileType = event.target.files[0].type;
            console.log('FILE-TYPE', fileType);
            if (fileType.match(/image|video\/*/) == null) {
                attachment.error = 'File not supported.';
                await setHeroAttachment(attachment);
                return;
            } else {
                console.log('I-m in decision');
                let content_type = '';
                if (fileType.match(/image\/*/)) {
                    console.log('Got Image');

                    content_type = 'image';
                    // setPost({ ...post, content_type: 'image' });
                    // attachment.contentType = 'image';

                } else if (fileType.match(/video\/*/)) {
                    console.log('Got Video');

                    content_type = 'video';

                    // setPost({ ...post, content_type: 'video' });
                    // attachment.contentType = 'video'
                }

                console.log('Decision....Image/Video', content_type);

                // setPost({ ...post, content_type: content_type });
                tempPost.content_type = content_type;
                attachment.error = '';
            }

            const reader = new FileReader();
            reader.readAsDataURL(event.target.files[0]);
            reader.onload = () => {
                attachment.file = reader.result;
                setHeroAttachment(attachment);
                // setPost({ ...post, featured_img: reader.result })
            }
            tempPost.featured_img = event.target.files[0];

            await setPost(tempPost);


        }
    };

    const getCourse = (courseId) => {
        setIsWaiting(true);

        AdminApis.getCourse(courseId)
            .then(res => {
                const data = res.data.result;
                // if (data.custom_fields) {
                //     setCustomFields(JSON.parse(data.custom_fields));
                // }

                setIsWaiting(false);
                setPost(data);
                if (res.data.result.featured_img) {
                    // let attachment = {
                    //     fileName: event.target.files[0].name,
                    // }
                    console.log('featured_img...', baseURL + res.data.result.featured_img);
                    setHeroAttachment({ file: (baseURL + res.data.result.featured_img) })
                    // this.featuredAttachmentUrl = this.baseUrl + '/' + response.result.featured_img;
                }
                // setIsCreated(true);
                // NotifierService.notify('success', res.data.success);
                // navigate(params.content_id > 0 ? ('/admin/cms/posts/' + params.content_id) : '/admin/cms')
            })
            .catch(err => {
                console.log(err);
                setIsWaiting(false);
            })
    }

    const saveCourse = () => {
        setIsSaving(true);
        // const type = update ? 'update' : 'create';
        const type = action.update ? 'update' : 'create';
        const data = getFormData();
        AdminApis.saveCourse(type, data)
            .then(res => {
                setIsSaving(false);
                console.log('SAVE-RESPONSE...', res.data);

                if (!action.update) {
                    navigate(-1);
                }
                // setIsCreated(true);

                Notifier.notify('success', res.data.success);
                // navigate(params.content_id > 0 ? ('/admin/cms/posts/' + params.content_id) : '/admin/cms')
            })
            .catch(err => {
                console.log(err);
                setIsSaving(false);
            })
    }

    const getFormData = () => {
        const formData = new FormData();

        formData.append('title_en', post.title_en);
        formData.append('title_bg', post.title_bg);

        formData.append('short_desc_en', post.short_desc_en);
        formData.append('short_desc_bg', post.short_desc_bg);

        formData.append('about_en', post.about_en);
        formData.append('about_bg', post.about_bg);

        formData.append('about_en', post.about_en);
        formData.append('about_bg', post.about_bg);

        formData.append('featured_img', post.featured_img);

        formData.append('key_features', JSON.stringify(post.key_features));
        formData.append('skills', JSON.stringify(post.skills));
        formData.append('languages', JSON.stringify(post.languages));
        formData.append('instructors', JSON.stringify(post.instructors));
        formData.append('order_course', post.order_course || '');


        // formData.append('label', post.label);
        // formData.append('content_en', post.content_en);
        // formData.append('content_bg', post.content_bg);
        // formData.append('order_cms', post.order_cms ? post.order_cms : '');
        // formData.append('content_type', post.content_type);

        // formData.append('custom_fields', JSON.stringify(customFields.map(({ file, ...rest }) => rest)));

        if (action.update) {
            // formData.append('id', post.id ? post.id : postQuery.post_id);
            formData.append('id', post.id || '');
        }

        return formData;
    }

    const updateCourseStatus = () => {
        setStatusLoader(true);
        // const payload = { post_id: postQuery.post_id, status: post.status === 'publish' ? 'pending' : 'publish' };
        const payload = { course_id: post.id, status: post.status === 'publish' ? 'pending' : 'publish' }

        AdminApis.changeCourseStatus(payload)
            .then(res => {
                setStatusLoader(false);
                console.log('SAVE-RESPONSE...', res.data);
                let alertMsg = ''
                if (res.data.result === 'publish') {
                    setPost({ ...post, status: res.data.result });
                    alertMsg = 'Post Published.';
                } else {
                    alertMsg = 'Post Deactivated.';
                    setPost({ ...post, status: res.data.result });
                }
                // navigate();
                // setIsCreated(true);
                Notifier.notify('success', alertMsg);
                // navigate(params.content_id > 0 ? ('/admin/cms/posts/' + params.content_id) : '/admin/cms')

            })
            .catch(err => {
                console.log(err);
                setStatusLoader(false);
            })
    }

    const handleKeyFeatures = (type = 'add', index = null) => {
        let temp = [...post.key_features];

        if (type === 'delete' && index > -1) {
            temp.splice(index, 1);
            // temp[index].attachment = '';
        }

        if (type === 'add') {
            temp.push({ feature_en: '', feature_bg: '' });
        }

        setPost({ ...post, key_features: temp });
    }

    const handleSkills = (type = 'add', index = null) => {
        let temp = [...post.skills];

        if (type === 'delete' && index > -1) {
            temp.splice(index, 1);
            // temp[index].attachment = '';
        }

        if (type === 'add') {
            temp.push({ skill_en: '', skill_bg: '' });
        }

        setPost({ ...post, skills: temp });
    }

    const selectLanguage = (e) => {
        const selectedValue = e.target.value;
        const languageObj = languages.find(lang => lang.key === selectedValue);
        if (languageObj && !post.languages.includes(languageObj.key)) {
            const updatedLangs = [...post.languages];
            updatedLangs.push(languageObj.key);
            setPost({ ...post, languages: updatedLangs });
        }
    };

    const removeLanguage = (lang) => {
        console.log('lang', lang);
        const updatedLangs = [...post.languages];
        const index = updatedLangs.indexOf(lang);
        if (index !== -1) {
            updatedLangs.splice(index, 1);
        }
        setPost({ ...post, languages: updatedLangs });
    };

    const handleInstructor = (type = 'add', idx = null) => {
        const tempIns = [...post.instructors];

        if (type === 'add' && instructorInput) {
            tempIns.push(instructorInput);
            setInstructorInput('');
        }
        if (type === 'remove' && idx !== null) {
            tempIns.splice(idx, 1);
        }
        setPost({ ...post, instructors: tempIns });
    };

